// 人脸识别开启会话
export const createFaceLivenessSession = async params => {
  return await request.post(`/rekognition.rekognition/createFaceLivenessSession`, params)
}
// 录入面孔
export const indexFaces = async params => {
  return await request.post(`/rekognition.rekognition/indexFaces`, params)
}
// 删除面孔
export const deleteFaces = async params => {
  return await request.post(`/rekognition.rekognition/deleteFaces`, params)
}
// 比较面孔
export const compareFaces = async params => {
  return await request.post(`/rekognition.rekognition/compareFaces`, params)
}
// 验证图片是否真人
export const getDetectFaces = async params => {
  return await request.post(`/rekognition.rekognition/detectFaces`, params)
}
// 验证是否本人
export const getCompareFaceByImage = async params => {
  return await request.post(`/rekognition.rekognition/compareFaceByImage`, params)
}
