<script>
import { mapState } from 'vuex'
import bindInfo from '@/components/ui/bind-info'
import SiteMap from '@/components/ui/site-map'
import humanFace from '@/components/ui/human-face'
import _common from '@style/views/mine.modules.scss'
import { setBirthday } from '@/api/modules/login.js'
import CpfBind from '@/components/ui/cpf-bind'

export default {
  name: 'UserAccount',
  components: {
    bindInfo,
    SiteMap,
    humanFace,
    CpfBind
  },
  data() {
    return {
      dialogState: false,
      bindData: '',
      cpfdialogState: false
    }
  },
  computed: {
    ...mapState('base', ['isApp', 'user', 'languageType', 'systemConfig']),
    ...mapState('source', ['phonePlaces']),
    checkPhone() {
      return !!this.user.tel
    },
    checkEmail() {
      return !!this.user.account
    },
    accountPhone() {
      const { area, tel } = this.user
      if (!tel) return this.$t('UI_Not_Nound')
      return `+${area} ${tel}`
    },
    // 是否开启生日开关 0关 1开
    isBirthday() {
      const { birthday_switch = 0 } = this.systemConfig

      return birthday_switch == 1
    },
    // 生日日期信息
    birthdayInfo() {
      const { birthday = 0 } = this.user
      if (!birthday) return null

      const info = this.$u.datetime.getDate__useTimestamp(birthday * 1000)
      return info.split('-')
    },
    // 人脸识别开关 0关 1开
    isFaceSwitch() {
      const { rekognition_switch = 0 } = this.systemConfig

      return rekognition_switch == 1
    },
    // 人脸识别绑定状态 0未认证 1已认证
    isFaceStatus() {
      const { rekognition_status = 0 } = this.user

      return rekognition_status == 1
    },
    // 是否允许用户删除人脸记录 0不允许 1允许
    isDeleteFace() {
      const { aws_record_unbind_by_user = 0 } = this.systemConfig
      return aws_record_unbind_by_user == 1
    },
    // cpf 绑定
    checkCpf() {
      return !!this.user.reg_bind_cpf
    }
  },
  methods: {
    loadVariables: (key = '') => _common[key],
    // 修改绑定邮箱或手机号或密码
    onEditEmailPhonePwd(sign = 'email') {
      this.bindData = sign
      this.dialogState = true
    },
    onCpfFn() {
      this.cpfdialogState = true
    },
    backClick() {
      this.dialogState = false
    },
    // 生日设置按钮
    onBirthday() {
      const _this = this
      this.$pop
        .birthday({
          success: async (date = 0) => {
            const birthday = date ? date / 1000 : 0
            const params = {
              token: _this.user.token,
              language: _this.languageType,
              birthday
            }

            const res = await setBirthday(params).catch(e => e)
            if (!res || res.code != 0) return

            this.$store.commit('base/SET_BIRTHDAY', birthday)
            this.$pop.message().show({ content: res.msg })
          }
        })
        .show()
    },
    // 人脸识别弹窗打开
    async onHumanFace() {
      // compare_type 1=绑定，2=解绑，3=校验人脸
      const compare_type = this.isFaceStatus ? 2 : 1

      if (this.$refs['faceRef']) {
        const res = await this.$refs['faceRef'].show({ compare_type }).catch(e => e)
        if (res == 'ok')
          this.$store.commit('base/SET_USER', { ...this.user, rekognition_status: this.isFaceStatus ? 0 : 1 })
      }
    },
    // cpf 绑定
    async onCpfSubmit(info) {
      this.cpfdialogState = false
    },
    // cpf 绑定弹窗关闭
    onClose() {
      this.cpfdialogState = false
    }
  }
}
</script>

